<template>
  <div style="margin-top: 40px">
    <div class="accidents-area">
     <!-- <el-row>
        <el-col
          v-for="accident in accidents"
          :key="accident.id"
          :span="6"
        >
          <el-card :span="8" :body-style="{ padding: '0px' }" class="box-card"  shadow="hover">
           <router-link class="accident-link" :to="{path:'accidents/details',query:{id: accident.id}}">
            <span style="font-size: 20px">ID:{{accident.id}}</span></router-link>
            <div><span style="font-size: 20px">{{accident.intro}}</span></div>
            <video controls width="210" :src="accident.videoUrl" type="video/mp4"></video>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-pagination
      background
      layout="total, prev, pager, next, jumper"
      @current-change="handleCurrentChange"
      :page-size="pageSize"
      :total="total">
    </el-pagination> -->

    <el-row>
      <el-col
          v-for="accident in accidents"
          :key="accident.id"
          :span="6"
      >

      <div class="card">
        <div class="card-details">
          <p class="text-title">{{accident.intro}}ID:{{accident.id}}</p>
          <p class="text-body"><video controls width="210" :src="accident.videoUrl" type="video/mp4"></video></p>
        </div>
        <router-link class="accident-link" :to="{path:'accidents/details',query:{id: accident.id}}">
            <span class="card-button">更多信息</span>
        </router-link>
      </div>
      </el-col>
    </el-row>
    </div>
    <el-pagination
    background
      layout="total, prev, pager, next, jumper"
      @current-change="handleCurrentChange"
      :page-size="pageSize"
      :total="total"
      class="pagination">
    </el-pagination>

  </div>
</template>

<script>

  export default {
      name: 'Accidents',
      data () {
        return {
          accidents: [],
          pageSize: 12,
          total: 0,
          r: 0, // 用于判断是否是筛选后的分页
          did: 0,
          tid: 0,
          oid: 0,
          nid: 0,
          aid: 0
          }
        },
      mounted: function () {
        this.loadAccidents()
      },
      methods: {
        loadAccidents () {
          var _this = this
          var testId = this.$route.query.id
          this.$axios.get('/accidents/' + testId + '/' + this.pageSize + '/1').then(resp => {
            if (resp && resp.data.code === 200) {
              _this.accidents = resp.data.result.content
              _this.total = resp.data.result.totalElements
            }
          })
        },
        handleCurrentChange (page) {
          var _this = this
          var testId = this.$route.query.id
          if (_this.r === 0) {
           this.$axios.get('/accidents/' + testId + '/' + this.pageSize + '/' + page).then(resp => {
              if (resp && resp.data.code === 200) {
                _this.accidents = resp.data.result.content
                _this.total = resp.data.result.totalElements
             }
           })
          } else {
            var url = 'filter/' + this.did + '/' + this.tid + '/' + this.oid + '/' + this.nid + '/' + this.aid + '/' + testId + '/' + this.pageSize + '/' + page + '/accidents'
            this.$axios.get(url).then(resp => {
              if (resp && resp.data.code === 200) {
                _this.accidents = resp.data.result.content
                _this.total = resp.data.result.totalElements
              }
            })
          }
        }
      }
    }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-family: '微软雅黑', Arial, Helvetica, sans-serif;
    box-sizing: border-box;
    list-style: none;
  }

  div {
    font-size: 12px;
  }

  span {
    font-size: 12px;
  }
.accidents-area {
  width: 1200px;/*修改下面那个盒子的大小 */
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.cardvideo{
  height: 130px;
}
.pagination{
  margin-left: 35%;
}
.card {
 width: 230px;
 height: 180px;
 border-radius: 20px;
 background: rgb(134, 134, 0,0);
 position: relative;
 padding: 1.8rem;
 margin: 30px 20px;
 border: 2px solid #c3c6ce;
 transition: 0.5s ease-out;
  /* overflow: visible; */
}

.card-details {
color: rgba(39,63,138,1);
 text-align: center;
 font-family: 'Roboto Slab', serif;
 height: 100%;
 gap: .5em;
 display: grid;
 place-content: center;
}

.card-button {
  text-align: center;
  transform: translate(-50%, 125%);
  width: 60%;
  border-radius: 1rem;
  border: none;
  background-color: #008bf8;
  color: #fff;
  font-size: 1rem;
  font-family: 'Roboto Slab', serif;
  padding: .5rem 1rem;
  position: absolute;
  left: 50%;
  bottom: 0;
  opacity: 0;
  transition: 0.3s ease-out;
}

.text-body {
 color: rgb(134, 134, 134);
}

/*Text*/
.text-title {
 font-size: 1.5em;
 font-weight: bold;
 font-family: 'Roboto Slab', serif;
}

/*Hover*/
.card:hover {
 border-color: #008bf8;
 box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.25);
}

.card:hover .card-button {
 transform: translate(-50%, 50%);
 opacity: 1;
}

</style>



