<template>
  <el-container class="elcontainer">
    <el-header class="elheader">
      <accidentFilter @indexSelect="listByFilter" ref="accidentFilter"></accidentFilter>
    </el-header>
    <el-main>
      <accidents class="accidents-area" ref="accidentArea"></accidents>
    </el-main>
  </el-container>
</template>

<script>
  import AccidentFilter from './AccidentFilter'
  import Accidents from './Accidents'

  export default {
    name: 'AccidentIndex',
    components: {AccidentFilter, Accidents},
    methods: {
      listByFilter () {
        var _this = this
        var did = this.$refs.accidentFilter.did
        var tid = this.$refs.accidentFilter.tid
        var oid = this.$refs.accidentFilter.oid
        var nid = this.$refs.accidentFilter.nid
        var aid = this.$refs.accidentFilter.aid
        var testId = this.$refs.accidentArea.$route.query.id
        var size = this.$refs.accidentArea.pageSize
        var url = 'filter/' + did + '/' + tid + '/' + oid + '/' + nid + '/' + aid + '/' + testId + '/' + size + '/1' + '/accidents'
        this.$axios.get(url).then(resp => {
          if (resp && resp.data.code === 200) {
            _this.$refs.accidentArea.accidents = resp.data.result.content
            _this.$refs.accidentArea.total = resp.data.result.totalElements
            _this.$refs.accidentArea.did = did
            _this.$refs.accidentArea.tid = tid
            _this.$refs.accidentArea.oid = oid
            _this.$refs.accidentArea.nid = nid
            _this.$refs.accidentArea.aid = aid
            _this.$refs.accidentArea.r = 1
          }
        })
      }
    }
  }
</script>

<style scoped>
  *{
    margin: 0;
    padding: 0;
  }
  .elheader{
    height: auto !important;
    margin: 0 auto;
    padding: auto;
    /* background-color: pink; */
  }
  .scenarios-area {
    /* width: 2000px; */
    margin: 100px auto;
    padding: 20px;
    /* background-color: black; */
  }
  /* 最大的底层模块 */
  .elcontainer{
    background-image: url(../../assets/img/bg/library_bgc.png);
    background-size: cover;
    width: 100%;
    padding: 0;
  }
</style>



