<template>
 <div class="AccidentFilter">
      <el-card  class="elcard2">
      <div class="upperbox">
      <tr>
        <td class="">
          车道类型:
        </td>
        <td>
          <input type="checkbox" id="100" value="100" v-model="drivewayTypes" />
          <label for="单车道">单车道</label>
        </td>
        <td>
          <input type="checkbox" id="010" value="10" v-model="drivewayTypes" />
          <label for="多车道">多车道</label>
        </td>
        <td>
          <input type="checkbox" id="001" value="1" v-model="drivewayTypes" />
          <label for="交叉路口">交叉路口</label>
        </td>
      </tr>
      <tr>
        <td class="">
          规划任务:
        </td>
        <td>
          <input type="checkbox" id="100" value="100" v-model="taskPlannings" />
          <label for="直行">直行</label>
        </td>
        <td>
          <input type="checkbox" id="010" value="10" v-model="taskPlannings" />
          <label for="转弯">转弯</label>
        </td>
        <td>
          <input type="checkbox" id="001" value="1" v-model="taskPlannings" />
          <label for="变道">变道</label>
        </td>
      </tr>
      <tr>
        <td class="">
          障碍物类型:
        </td>
        <td>
          <input type="checkbox" id="1000" value="1000" v-model="obstacleTypes" />
          <label for="行人">行人</label>
        </td>
        <td>
          <input type="checkbox" id="0100" value="100" v-model="obstacleTypes" />
          <label for="路障">路障</label>
        </td>
        <td>
          <input type="checkbox" id="0010" value="10" v-model="obstacleTypes" />
          <label for="自行车">自行车</label>
        </td>
        <td>
          <input type="checkbox" id="0001" value="1" v-model="obstacleTypes" />
          <label for="机动车">机动车</label>
        </td>
      </tr>
      <tr>
        <td class="">
          障碍物数量:
        </td>
        <td>
         <input type="radio" id="1000" value="1000" v-model="obstacleNumbers" />
         <label for="<10">&lt;10</label>
        </td>
        <td>
         <input type="radio" id="0100" value="100" v-model="obstacleNumbers" />
         <label for="[10, 20)">[10, 20)</label>
        </td>
        <td>
          <input type="radio" id="0010" value="10" v-model="obstacleNumbers" />
          <label for="[20, 30)">[20, 30)</label>
        </td>
        <td>
          <input type="radio" id="0001" value="1" v-model="obstacleNumbers" />
          <label for=">=30">&gt;=30</label>
        </td>
      </tr>
      <tr>
        <td class="">
          事故类型:
        </td>
        <td>
          <input type="checkbox" id="10" value="10" v-model="targetAccidents" />
          <label for="碰撞">碰撞</label>
        </td>
        <td>
          <input type="checkbox" id="01" value="1" v-model="targetAccidents" />
          <label for="驶出道路">驶出道路</label>
        </td>
      </tr>
      </div>
      <button @click="handleSelect" class="cta">
        <span>筛 选</span>
        <svg viewBox="0 0 13 10" height="10px" width="15px">
          <path d="M1,5 L11,5"></path>
          <polyline points="8 1 12 5 8 9"></polyline>
        </svg>
      </button>
    </el-card>
    </div>
</template>

<script>
    export default {
      name: 'AccidentFilter',
      data () {
        return {
          drivewayTypes: [],
          taskPlannings: [],
          obstacleTypes: [],
          obstacleNumbers: [],
          targetAccidents: []
        }
      },
      methods: {
        handleSelect () {
          var s1 = 0
          var s2 = 0
          var s3 = 0
          var s4 = 0
          var s5 = 0
          for (var i = 0; i < this.drivewayTypes.length; i++) {
            s1 += Number(this.drivewayTypes[i])
          }
          this.did = s1
          for (var j = 0; j < this.taskPlannings.length; j++) {
            s2 += Number(this.taskPlannings[j])
          }
          this.tid = s2
          for (var k = 0; k < this.obstacleTypes.length; k++) {
            s3 += Number(this.obstacleTypes[k])
          }
          this.oid = s3
          for (var m = 0; m < this.obstacleNumbers.length; m++) {
            s4 += Number(this.obstacleNumbers[m])
          }
          this.nid = s4
          for (var n = 0; n < this.targetAccidents.length; n++) {
            s5 += Number(this.targetAccidents[n])
          }
          this.aid = s5
          this.$emit('indexSelect')
        }
      }
    }
</script>

<style scoped>
    * {
      margin: 0;
      padding: 0;
      font-family: 'Roboto Slab', serif;      box-sizing: border-box;
    }

    body {
      font-size: 12px;
      width: 1200px;
    }

    div {
      font-size: 12px;
    }

    span {
      font-size: 12px;
    }
    .AccidentFilter{
      margin: 0 auto;
    }

@supports (-webkit-appearance: none) or (-moz-appearance: none) {
  input[type=checkbox],
input[type=radio] {
    --active: #275EFE;
    --active-inner: #fff;
    --focus: 2px rgba(39, 94, 254, .3);
    --border: #BBC1E1;
    --border-hover: #275EFE;
    --background: #fff;
    --disabled: #F6F8FF;
    --disabled-inner: #E1E6F9;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 21px;
    outline: none;
    display: inline-block;
    vertical-align: top;
    position: relative;
    margin: 0;
    cursor: pointer;
    border: 1px solid var(--bc, var(--border));
    background: var(--b, var(--background));
    transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;
  }
  input[type=checkbox]:after,
input[type=radio]:after {
    content: "";
    display: block;
    left: 0;
    top: 0;
    position: absolute;
    transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s);
  }
  input[type=checkbox]:checked,
input[type=radio]:checked {
    --b: var(--active);
    --bc: var(--active);
    --d-o: .3s;
    --d-t: .6s;
    --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
  }
  input[type=checkbox]:disabled,
input[type=radio]:disabled {
    --b: var(--disabled);
    cursor: not-allowed;
    opacity: 0.9;
  }
  input[type=checkbox]:disabled:checked,
input[type=radio]:disabled:checked {
    --b: var(--disabled-inner);
    --bc: var(--border);
  }
  input[type=checkbox]:disabled + label,
input[type=radio]:disabled + label {
    cursor: not-allowed;
  }
  input[type=checkbox]:hover:not(:checked):not(:disabled),
input[type=radio]:hover:not(:checked):not(:disabled) {
    --bc: var(--border-hover);
  }
  input[type=checkbox]:focus,
input[type=radio]:focus {
    box-shadow: 0 0 0 var(--focus);
  }
  input[type=checkbox],
input[type=radio] {
    width: 21px;
  }
  input[type=checkbox]:after,
input[type=radio]:after {
    opacity: var(--o, 0);
  }
  input[type=checkbox]:checked,
input[type=radio]:checked {
    --o: 1;
  }
  input[type=checkbox] + label,
input[type=radio] + label {
    font-size: 24px;
    /* line-height: 43x; */
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    margin-left: 4px;
  }

  input[type=checkbox] {
    border-radius: 7px;
  }
  /*设置里面的勾勾大小 */
  input[type=checkbox]:after {
    width: 10px;
    height: 18px;
    border: 3px solid var(--active-inner);
    border-top: 0;
    border-left: 0;
    left: 11px;
    top: 4px;
    transform: rotate(var(--r, 8deg));
  }
  input[type=checkbox]:checked {
    --r: 43deg;
  }

  input[type=radio] {
    border-radius: 50%;
  }
  input[type=radio]:after {
    width: 10px;
    height: 18px;
    border: 3px solid var(--active-inner);
    border-top: 0;
    border-left: 0;
    left: 11px;
    top: 4px;
    transform: rotate(var(--r, 8deg));
  }
  input[type=radio]:checked {
    --r: 43deg;
  }
}
ul {
  margin: 12px;
  padding: 0;
  list-style: none;
  width: 100%;
  max-width: 320px;
}
ul li {
  margin: 16px 0;
  position: relative;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}
*:before, *:after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  font-family: 'Roboto Slab', serif;  color: #8A91B4;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F6F8FF;
}
@media (max-width: 800px) {
  body {
    flex-direction: column;
  }
}
.upperbox{
  text-align: left;
  padding-left: 12%;
}
/* .upperbox div{
  margin-right: 50px;
} */
.upperbox tr td{
  padding: 10px 20px;
  font-size: 30px;
  font-weight: 700;
  font-family: 'Roboto Slab', serif;
  color: #fff;
  vertical-align: middle;
  align-self: center;
}
.upperbox tr td input{
  display: inline-block;
  margin-top: 10px;
  width: 34px;
  height: 34px;
}
/* .upperbox tr td label{
  display: inline-block;
  height: 30px;
} */
/* 删了的筛选条件css */
h1{
  text-align: left;
  margin-bottom: 30px;
  margin-left: 50px;
  /* text-shadow: pink; */
  color: rgb(69, 102, 115);
}
.elcard{
  text-align: center;
  width: 1135px;
  height:250px;
  margin: 30px auto;
  background-color: pink;
  border-radius: 20px;
  --trans-light: rgba(255, 255, 255, 0.75);
  --trans-dark: rgba(25, 25, 25, 0.75);
  --border-style: 1px solid rgb(169, 169, 169);
  --backdrop-filter: blur(5px) saturate(150%);

  background: linear-gradient( -45deg, #e8d8b9, #eccec5, #a3e9eb, #bdbdf0, #eec1ea );
    box-shadow: 0 0 5px rgb(66 68 68);
    position: relative;
    background-size: 400% 400%;
}
@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-moz-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
/*筛选按钮 */
.cta {
 position: relative;
 /* margin: auto; */
 /* margin-right: 15%; */
 margin-left: 65%;
 padding: 12px 18px;
 transition: all 0.2s ease;
 border: none;
 background: none;
}

.cta:before {
 content: "";
 position: absolute;
 top: 0;
 left: 0;
 display: block;
 border-radius: 50px;
 background: #3b66e7;
 width: 45px;
 height: 45px;
 transition: all 0.3s ease;
}

.cta span {
 position: relative;
 font-size: 18px;
 font-weight: 700;
 letter-spacing: 0.05em;
 color: #fff;
 font-family: 'Roboto Slab', serif;
}

.cta svg {
 position: relative;
 top: 0;
 margin-left: 10px;
 fill: none;
 stroke-linecap: round;
 stroke-linejoin: round;
 stroke: #273f8a;
 stroke-width: 2;
 transform: translateX(-5px);
 transition: all 0.3s ease;
}

.cta:hover:before {
 width: 100%;
 background:  #97c2ff;
}

.cta:hover svg {
 transform: translateX(0);
}

.cta:active {
 transform: scale(0.95);
}
/* 修改顶部大方块 */
.AccidentFilter .elcard2{
  text-align: center;
  width: 1350px;
  height:400px;
  margin: 30px auto;
  background-color:rgba(15, 15, 176, 0);
  border: 0px;
  box-shadow: none;
}
</style>



